"use client"

import { useEffect, useState } from "react"
import { DynamicMap } from "./dynamic-map"
import { type GeolocationData } from "@/lib/services/geo-service"

interface LiveLocationMapProps {
  height?: string
  showAccuracyCircle?: boolean
  autoCenter?: boolean
  initialLocation?: GeolocationData
}

export default function LiveLocationMap({
  height = "400px",
  showAccuracyCircle = true,
  autoCenter = true,
  initialLocation
}: LiveLocationMapProps) {
  const [location, setLocation] = useState<GeolocationData | null>(initialLocation || null)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    if (initialLocation) {
      setLocation(initialLocation)
      return // No iniciar geolocalizaci√≥n si hay ubicaci√≥n inicial
    }

    if (!navigator.geolocation) {
      setError("Geolocalizaci√≥n no soportada")
      return
    }

    let watchId: number | undefined

    const handleSuccess = (position: GeolocationPosition) => {
      setLocation({
        latitude: position.coords.latitude,
        longitude: position.coords.longitude,
        accuracy: position.coords.accuracy,
        altitude: position.coords.altitude,
        speed: position.coords.speed,
        heading: position.coords.heading,
        timestamp: position.timestamp
      })
      setError(null)
    }

    const handleError = (error: GeolocationPositionError) => {
      console.error("Error getting location:", error)
      setError("Error al obtener ubicaci√≥n")
    }

    const options = {
      enableHighAccuracy: true,
      timeout: 5000,
      maximumAge: 0
    }

    if (autoCenter) {
      watchId = navigator.geolocation.watchPosition(
        handleSuccess,
        handleError,
        options
      )
    } else {
      navigator.geolocation.getCurrentPosition(
        handleSuccess,
        handleError,
        options
      )
    }

    return () => {
      if (watchId) {
        navigator.geolocation.clearWatch(watchId)
      }
    }
  }, [autoCenter, initialLocation])

  if (error) {
    return (
      <div
        style={{ height }}
        className="flex items-center justify-center bg-gray-900 rounded-lg"
      >
        <div className="text-red-400">{error}</div>
      </div>
    )
  }

  if (!location) {
    return (
      <div
        style={{ height }}
        className="flex items-center justify-center bg-gray-900 rounded-lg"
      >
        <div className="animate-spin rounded-full h-8 w-8 border-2 border-current" />
      </div>
    )
  }

  return (
    <DynamicMap
      height={height}
      onLocationUpdate={setLocation}
      showAccuracyCircle={showAccuracyCircle}
      autoCenter={autoCenter}
      zoom={15}
    />
  )
}
